import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';
import PropTypes from 'prop-types';
import Select from 'react-select';
import Aux from "../../hoc/_Aux";

import DatePicker from 'react-datepicker';
import AsyncSelect from 'react-select/async';


import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import Loader from '../../App/components/Reusables/Loader'
import { createAccount, getAccountTypes, getAccounts } from '../../api/accounting/account';
import { validateAccountCreation } from '../../validation/account';
import { getCustomers } from '../../api/sales/customer';
import { get } from 'jquery';
import { getVendors } from '../../api/inventory/vendorApi';
import CustomSelectMenu from '../../App/components/Reusables/CustomSelectMenu';
import isEmpty from '../../validation/isEmpty';

import CreateAccountTypeModal from '../AccountTypes/CreateAccountTypeModal';
import { RESET_FETCH_MORE_OPTIONS, SET_FETCH_MORE_OPTIONS, SET_INSTANCE_CREATION_MODAL, SET_IS_MENU_OPEN } from '../../redux/actions/types';
import store from '../../redux/store';
import { connect } from 'react-redux';
import DEMO from '../../globals/constant';
import CustomSelectMenuList from '../../App/components/Reusables/CustomSelectMenuList';

const Create = (props) => {

    const [loading, setLoading] = React.useState(false);
    const [submitting, setSubmitting] = React.useState(false)
    const [defaultAccountTypeOptions, setDefaultAccountTypeOptions] = React.useState([]);
    const [accountTypeOptionsMaxLenght, setAccountTypeOptionsMaxLength] = React.useState(0)

    const [customerOptionsMaxLength, setCustomerOptionsMaxLength] = React.useState(0)
    const [defaultCustomerOptions, setDefaultCustomerOptions] = React.useState([])

    const [partyInputText, setPartyInputText] = React.useState('')

    const [defaultVendorOptions, setDefaultVendorOptions] = React.useState([])
    const [vendorOptionsMaxLength, setVendorOptionsMaxLength] = React.useState(0)

    const [accountInputText, setAccountInputText] = React.useState('');
    const [defaultAccountOptions, setDefaultAccountOptions] = React.useState([]);
    const [accountOptionsMaxLength, setAccountOptionsMaxLength] = React.useState(0)

    const [accountData, setAccountData] = React.useState({
        typeInput: '',
        type: '',
        name: '',
        openingBalance: 0,
        openingDate: new Date(),
        hasParent: false,
        parentId: '',
        parentUuid: '',
        linkedWith: '',
        partyId: '',
        partyName: '',
    })

    console.log("The input party text is given as", partyInputText)

    const [errors, setErrors] = React.useState({});

    const getAccountTypes_ = async (signal) => {
        console.log('erhe wi one we have the fired the call of get')
        getAccountTypes({
            "action": 'get',
            filter: 'none',
            start: defaultAccountTypeOptions.length,
            end: defaultAccountTypeOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultAccountTypeOptions([
                        ...defaultAccountTypeOptions,
                        ...res.json.account_types.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, uuid: itm.uuid }))
                    ]);
                    setAccountTypeOptionsMaxLength(res.json.count);
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsAccounts', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err)
            });
    }

    const getCustomers_ = async (signal) => {
        await getCustomers({
            "action": 'get',
            filter: 'none',
            start: defaultCustomerOptions.length,
            end: defaultCustomerOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultCustomerOptions([
                        ...defaultCustomerOptions,
                        ...res.json.customers.map(instance => ({ label: `${instance.first_name} ${instance.middle_name} ${instance.last_name}`, value: instance.id }))
                    ]);
                    setCustomerOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsCustomers', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }

    const getAccounts_ = async (signal) => {
        getAccounts({
            "action": 'get',
            "filter": 'none',
            "start": defaultAccountOptions.length,
            "end": defaultAccountOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then((res) => {
                if (res.json.status) {
                    setDefaultAccountOptions([
                        ...defaultAccountOptions,
                        ...res.json.accounts.map((itm, index) => ({ label: `${itm.name}`, value: itm.id }))
                    ]);
                    setAccountOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsAccs', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err)
            });
    }

    const getVendors_ = async (signal) => {
        await getVendors({
            "action": 'get',
            "filter": 'none',
            "start": defaultVendorOptions.length,
            "end": defaultVendorOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultVendorOptions([
                        ...defaultVendorOptions,
                        ...res.json.vendors.map(instance => ({ label: `${instance.first_name} ${instance.middle_name} ${instance.last_name}`, value: instance.id }))
                    ]);
                    setVendorOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsVendors', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }


    React.useEffect(() => {
        const controller = new AbortController();
        getAccountTypes_(controller.signal);
        getCustomers_(controller.signal);
        getVendors_(controller.signal);
        getAccounts_(controller.signal);
        return () => controller.abort()
    }, []);

    React.useEffect(() => {
        setAccountData({ ...accountData, partyId: '', partyName: '' })
    }, [accountData.linkedWith])



    //for the search of the parent Accounts
    const loadParentOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getAccounts(requestJson)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    return res.json.accounts.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, uuid: itm.uuid }));
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err)
            });

    }


    //to get the linked with: vendors or customers
    const loadPartyOptions = async (inputText) => {
        const requestJson = {
            action: 'get',
            start: 0,
            end: 50,
            filter: 'name',
            first_name: inputText,
            name: inputText
        }
        if (accountData.linkedWith === 'customer') {
            return getCustomers(requestJson)
                .then((res) => {
                    console.log(res);
                    if (res.json.status) {
                        return res.json.customers.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, uuid: itm.uuid }));
                    } else {
                        Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                    }
                }).catch((err) => {
                    console.log(err);
                });
        }
        if (accountData.linkedWith === 'vendor') {
            return getVendors(requestJson)
                .then((res) => {
                    console.log(res);
                    if (res.json.status) {
                        return res.json.vendors.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, uuid: itm.uuid }));
                    } else {
                        Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                    }
                }).catch((err) => {
                    console.log(err);
                });
        }
    }


    //for the search of account types
    const loadAccountTypeOptions = (inputText) => {
        return getAccountTypes({ action: 'get', filter: 'multiple', filters: { name: inputText, header: null, status: null }, start: 0, end: 50 })
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    return res.json.account_types.map((instance) => ({ label: instance.name, value: instance.id }))
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err)
            });
    }

    const amendErrorOf = (field) => {
        setErrors({ ...errors, [field]: '' })
    }

    const __openingDateChange = (selectedDate) => {
        amendErrorOf('openingDate')
        setAccountData({ ...accountData, openingDate: selectedDate })
    }

    const _partyInputTextChange = (inputText) => {
        setPartyInputText(inputText)
    }
    const _scrollCustomerMenuToBottom = (e) => {
        e.preventDefault();
        console.log(customerOptionsMaxLength, defaultCustomerOptions.length)
        if (customerOptionsMaxLength > defaultCustomerOptions.length && !partyInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsCustomers', true]
            })
            getCustomers_();
        }
    }
    const _scrollVendorMenuToBottom = (e) => {
        e.preventDefault();
        if (vendorOptionsMaxLength > defaultVendorOptions.length && !partyInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsVendors', true]
            })
            getVendors_();
        }
    }


    //change in vendor or customer id
    const _selectedPartyChange = (party) => {
        amendErrorOf('partyId')
        setAccountData({ ...accountData, partyId: party.value, partyName: party.label })
    }


    const _scrollAccountMenuToBottom = (e) => {
        e.preventDefault();
        if (accountOptionsMaxLength > defaultAccountOptions.length && !accountInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsAccs', true]
            })
            getAccounts_();
        }
    }

    const _accountInputChange = (inputText) => {
        setAccountInputText(inputText);
    }

    const _selectedParentChange = (parent) => {
        amendErrorOf('parentId');
        setAccountData({ ...accountData, parentId: parent.value, parentUuid: parent.uuid })
    }

    const _accountDataChange = (e) => {
        amendErrorOf(e.target.name)
        const type = e.target.type
        if (e.target.name === 'hasParent') {
            setAccountData({ ...accountData, hasParent: e.target.checked })
        } else {
            setAccountData({ ...accountData, [e.target.name]: type === 'number' ? parseFloat(e.target.value) : e.target.value })
        }
    }


    const _scrollTypeMenuToBottom = (e) => {
        e.preventDefault();
        if (accountTypeOptionsMaxLenght > defaultAccountTypeOptions.length && !accountData.typeInput) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsAccounts', true]
            })
            getAccountTypes_();
        }
    }

    const _typeInputChange = (typeInputText) => {
        setAccountData({
            ...accountData,
            typeInputText
        })
    }

    const _selectedTypeChange = (type) => {
        setErrors({ ...errors, type: '' })
        try {
            setAccountData({
                ...accountData,
                type: type.value,
                typeName: type.label,
            })
        }
        catch (e) {
            setAccountData({
                ...accountData,
                type: '',
                typeName: '',
            })
        }
    }

    // console.log(accountData);
    const _submit = () => {
        //check if there are errors

        const { isValid, errors } = validateAccountCreation(accountData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        console.log('no errros');

        setSubmitting(true)

        //if no errros api call
        const requestJson = {
            "action": "add",
            "account_type": accountData.type,
            "name": accountData.name,
            "opening_balance": accountData.openingBalance,
            "opening_date": accountData.openingDate,
            "parent": accountData.hasParent,
            "parent_id": accountData.hasParent ? accountData.parentId : null,
            "parent_uuid": accountData.hasParent ? accountData.parentUuid : null,
            "customer": accountData.linkedWith === 'customer' ? accountData.partyId : null,
            "vendor": accountData.linkedWith === 'vendor' ? accountData.partyId : null
        }
        console.log(requestJson);
        createAccount(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Account created successfully!', background: '#dffff0' })
                    props.history.push('/accounts');
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5d6' })
                }
            }).catch((err) => {
                console.log(err)
                setSubmitting(false);
            });

    }


    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Account</h5>
                            <hr />
                            <Row>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Account Name*</Form.Label>
                                        <Form.Control name="name" value={accountData.name} onChange={_accountDataChange} type="text" placeholder="Account Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Label>Account Type*</Form.Label>

                                    <AsyncSelect
                                        placeholder='Select'
                                        isClearable={true}
                                        value={isEmpty(accountData.type) ? null : { label: accountData.typeName, value: accountData.type }}
                                        onChange={(instance) => _selectedTypeChange(instance)}
                                        defaultOptions={defaultAccountTypeOptions}
                                        loadOptions={loadAccountTypeOptions}
                                        cacheOptions={true}
                                        onInputChange={_typeInputChange}
                                        components={{
                                            Menu: CustomSelectMenu,
                                            MenuList: CustomSelectMenuList
                                        }}

                                        onMenuScrollToBottom={_scrollTypeMenuToBottom}
                                        onMenuClose={() => {
                                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['account', false] })
                                        }}
                                        onMenuOpen={() => {
                                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['account', true] })
                                        }}
                                    />

                                    <ErrorBody>{errors.type}</ErrorBody>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Opening Balance*</Form.Label>
                                        <Form.Control name="openingBalance" value={accountData.openingBalance} onChange={_accountDataChange} type="number" placeholder="Opening Balance" />
                                        <ErrorBody>{errors.openingBalance}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Opening Date*</Form.Label>
                                        <DatePicker onChange={__openingDateChange} placeholder="Pick Date" name="openingDate" selected={accountData.openingDate} />
                                        <ErrorBody>{errors.openingDate}</ErrorBody>
                                    </Form.Group>
                                </Col>


                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Linked With</Form.Label>
                                        <Form.Control name="linkedWith" value={accountData.linkedWith} onChange={_accountDataChange} as="select">

                                            <option value='' disabled >Select...</option>
                                            <option value="vendor">Vendor</option>
                                            <option value="customer">Customer</option>
                                        </Form.Control>
                                        <ErrorBody>{errors.linkedWith}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                {accountData.linkedWith ?
                                    <Col md={6}>
                                        <Form.Group  >
                                            <Form.Label>{accountData.linkedWith === 'customer' ? 'Customer' : 'Vendor'}</Form.Label>
                                            <AsyncSelect
                                                defaultOptions={accountData.linkedWith === 'customer' ? defaultCustomerOptions : defaultVendorOptions}
                                                value={accountData.partyId ? { value: accountData.partyId, label: accountData.partyName } : null}
                                                onChange={_selectedPartyChange}
                                                loadOptions={loadPartyOptions}
                                                placeholder='Type name...'
                                                onInputChange={_partyInputTextChange}
                                                components={{
                                                    MenuList: CustomSelectMenuList,
                                                }}
                                                onMenuScrollToBottom={accountData.linkedWith === 'customer' ? _scrollCustomerMenuToBottom : _scrollVendorMenuToBottom}
                                                onMenuClose={() => {
                                                    store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                                                    store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['vendor', false] })
                                                }}
                                                onMenuOpen={() => {
                                                    store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['vendor', true] })
                                                }}
                                            />
                                            <ErrorBody>{errors.partyId}</ErrorBody>
                                        </Form.Group>
                                    </Col>
                                    :
                                    null}

                                <Col md={2} xs={4} style={{ minWidth: '78px', paddingRight: 0 }}>
                                    <Form.Group  >
                                        <Form.Label>Parent</Form.Label>
                                        <Form.Check name="hasParent" onChange={_accountDataChange} style={{ padding: '5px', paddingLeft: '20px' }} type="checkbox" label="Has parent" />
                                        <ErrorBody>{errors.hasParent}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={4} xs={8}>
                                    <Form.Group  >
                                        <Form.Label></Form.Label>
                                        <AsyncSelect
                                            defaultOptions={defaultAccountOptions}
                                            onChange={_selectedParentChange}
                                            loadOptions={loadParentOptions}
                                            placeholder='Type parent name'
                                            onInputChange={_accountInputChange}
                                            components={{
                                                MenuList: CustomSelectMenuList,
                                            }}
                                            onMenuScrollToBottom={_scrollAccountMenuToBottom}
                                            onMenuClose={() => {
                                                store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', false] })
                                            }}
                                            onMenuOpen={() => {
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', true] })
                                            }}
                                        />
                                        <ErrorBody>{errors.parentId}</ErrorBody>
                                    </Form.Group>
                                </Col>


                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/accounts') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>
                        </Card.Body>
                    </Card>
                </Col>
                <CreateAccountTypeModal
                    show={props.instanceCreationModal.account.modalShowing}
                    onHide={() => store.dispatch({ type: SET_INSTANCE_CREATION_MODAL, payload: ['account', false] })}
                    setCreatedToSelected={(instance) => {
                        setAccountData({
                            ...accountData,
                            type: instance.value,
                            typeName: instance.label,
                        });
                        setDefaultAccountTypeOptions([{ ...instance }, ...defaultAccountTypeOptions])
                    }}
                />
            </Row>
        </Aux>
    );
}



Create.propTypes = {
    instanceCreationModal: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
    instanceCreationModal: state.instanceCreationModal,
})

export default connect(mapStateToProps, {})(Create);
